import { h, Component } from 'preact';
import { observer } from 'mobx-preact';
import util from 'preact-util';

import wutil from '../../lib/wifet-util';
import LineGraph from '../../components/lineGraph';

const initialState = {};

@observer
class Strategy extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ...initialState,
        };
    }

	async loadAll() {
		const { windmillStore, weatherYrStore, productionStore, priceStore, demandStore } = this.props.stores;
		const { currentWindparkName, windparkCapacity } = windmillStore;

		const xMin = wutil.getDateDiffTime(-4);
		const xMax = wutil.getDateDiffTime(10);
		const now = Math.floor(new Date().getTime() / 1000);
		this.setState({ now, xMin, xMax });

		await this.loadYrData();
		await this.loadOpenData();
		const forcastYrData = weatherYrStore.getWindSpeeds(currentWindparkName);
		priceStore.generateData();

		const { prices } = priceStore;
		productionStore.generateData(forcastYrData, prices, windparkCapacity);

		demandStore.generateData();
	}

	async loadYrData() {
		const { weatherYrStore, windmillStore } = this.props.stores;
		const { currentWindpark } = windmillStore;

		weatherYrStore.updateQueryFilter({
			name: currentWindpark.name,
			altitude: currentWindpark.position.altitude,
			lat: currentWindpark.position.lat,
			lon: currentWindpark.position.lon,
		});
		return await weatherYrStore.load();
	}

	async loadOpenData() {
		const { weatherOpenStore, windmillStore } = this.props.stores;
		const { currentWindpark } = windmillStore;

		weatherOpenStore.updateQueryFilter({
			name: currentWindpark.name,
			lat: currentWindpark.position.lat,
			lon: currentWindpark.position.lon,
		});
		return await weatherOpenStore.load();
	}

	setWindpark = (e) => {
		const { windmillStore } = this.props.stores;
		const windpark = e.target.dataset.windpark;
		windmillStore.setCurrentWindparkName(windpark);
		this.loadAll();
	}

	componentDidMount() {
		this.loadAll();
	}

	render() {
		const { now, xMin, xMax } = this.state;
		const { windmillStore, productionStore, demandStore } = this.props.stores;
		const { demands: demandData, demandAi } = demandStore;

		const { windparks, currentWindparkName, windparkCapacity, windparkMaxCapacity } = windmillStore;
		const windparkCapacityPercent = windparkCapacity / windparkMaxCapacity * 100
		const { productionData, productionBoostData, productionLongLifeData } = productionStore;

		return (
			<div class='container-fluid mt-5 pt-2'>
				<div class='row'>
					<div class='col-4'>
						<h1><i class='fas fa-chess-knight' /> Strategy - {currentWindparkName}</h1>
						<span class='text-muted'>Running at</span> {util.format(windparkCapacityPercent, 1)}%
						<span class='text-muted'>, {windparkCapacity}/{windparkMaxCapacity} Mw</span>
					</div>
					<div class='col-8 pt-2'>
						<ul class='nav nav-pills float-right'>
						{windparks && windparks.map(wp => (
							<li class='nav-item'>
								<button
									class={`btn btn-link nav-link ${wp.name === currentWindparkName ? 'active' : ''}`}
									onClick={this.setWindpark}
									data-windpark={wp.name}
								>{wp.name}</button>
							</li>
						))}
						</ul>
					</div>
					<div class='col-12'>
						<LineGraph
							stores={this.props.stores}
							width={1200}
							height={500}
							showXTicks={1}
							xTicksVal={val => util.isoDate(val)}
							xMin={xMin}
							xMax={xMax}
							paddingTop={30}

							dataLeft={[demandData, demandAi]}
							legendLeft={['Demand', 'Demand AI prediction']}
							yMaxLeft={4000}
							yMinLeft={0}
							showYTicksLeft={1}
							yTicksLeft={['4TW', '3TW', '2TW', '1TW', '0TW']}
							unitLeft={['Mw', 'Mw']}
							avgWindowLeft={[2, 2]}
							smoothLeft={[1, 1]}

							dataRight={[productionData, productionBoostData, productionLongLifeData]}
							legendRight={['Normal production', 'Boost 10%', 'Long life -5%']}
							yMaxRight={4000}
							yMinRight={0}
							showYTicksRight={1}
							yTicksRight={['4TW', '3TW', '2TW', '1TW', '0TW']}
							unitRight={['Mw']}
							avgWindowRight={[1, 1, 1]}
							smoothRight={[1, 1, 1]}

							marker={now}
						/>
					</div>
				</div>
			</div>
		);
	}
}

export default Strategy;
